import { Tab4Root } from './../index';
import { FiltroVeiculo } from './../../models/filtro-veiculo';
import { MapaPage } from './../mapa/mapa';
import { Component, OnInit, ViewChild } from "@angular/core";
import { TranslateService } from "@ngx-translate/core";
import { IonicPage, NavController, Tabs } from "ionic-angular";
import { User } from '../../providers';
import { Tab1Root, Tab2Root, Tab3Root } from "../";
import { VeiculoService } from "../../providers/api/veiculo.service";
// import { BackgroundMode } from '@ionic-native/background-mode';

@IonicPage()
@Component({
  selector: "page-tabs",
  templateUrl: "tabs.html"
})
export class TabsPage {
  tab1Root: any = Tab1Root;
  tab2Root: any = Tab2Root;
  tab3Root: any = Tab3Root;
  tab4Root: any = Tab4Root;


  @ViewChild('myTabs') tabRef: Tabs;
  tab1Title = " ";
  tab2Title = " ";
  tab3Title = " ";
  tab4Title = " ";
  isInspection = false;
  constructor(
    public navCtrl: NavController,
    public translateService: TranslateService,
    public veiculoService: VeiculoService,
    public user: User,
    //private backgroundMode: BackgroundMode
  ) {
    translateService
      .get(["TAB1_TITLE", "TAB2_TITLE", "TAB3_TITLE","TAB4_TITLE"])
      .subscribe(values => {
        this.tab1Title = values["TAB1_TITLE"];
        this.tab2Title = values["TAB2_TITLE"];
        this.tab3Title = values["TAB3_TITLE"];
        this.tab4Title = "Inspections";
      });
  }

  ionViewDidLoad() {
    //this.backgroundMode.enable();
    
      this.veiculoService.iniciaTimeNotification();
    const funcao:String = this.user._user.perfil.funcao;
    if(funcao.indexOf('mobileinspection')>=0){
      this.isInspection = true;
    }
    
    this.veiculoService.tabRef = this.tabRef;
    /*const user = this.user._user;
    const filtro = new FiltroVeiculo();
    if (user.clienteId !== null) {
      if (user.clienteId === 1) {
        
      } else {
        filtro.clienteIdFilter = user.clienteFilho;
      }
      filtro.lstClienteId = user.clienteFilho;
    }
    if(user.clienteId !== 1){
      this.veiculoService.filtroVeiculos(filtro);
    }*/
  }
}



